.outline {
  margin: 0;
  padding: 0;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  top: 0;
  left: 0;
}

.content {
  display: flex;
  justify-content: space-between;
  padding: 40px;
  margin: 0;
  width: 95%;
}

.leftSide {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.rightSide {
  font-weight: 500;
  display: flex;
  column-gap: 20px;
}

.link {
  color: #1976d2;
  padding: 10px;
}

.login {
  color: #1f2937;
}

.line {
  border-right: 1px solid #d1d5db;
}
